.community-page {
  padding: 1rem;
  color: white;
  min-height: 100vh;
  box-sizing: border-box;
}

.community-container {
  margin: 0;
  text-align: center;
}

.community-container h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.community-container > p {
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

.communities-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.community-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  text-align: left;
}

.community-card h3 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #4CAF50;
}

.community-rules {
  margin-bottom: 1rem;
}

.community-rules p {
  margin-bottom: 0.3rem;
  opacity: 0.9;
  font-size: 0.8rem;
}

.community-card .btn {
  width: 100%;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  color: white;
  font-size: 1.2rem;
} 