.home-page {
  padding: 1rem;
  color: white;
  min-height: 100vh;
  box-sizing: border-box;
}

.hero-section {
  text-align: center;
  margin-bottom: 4rem;
}

.hero-section h1 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.hero-section p {
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
  opacity: 0.9;
  line-height: 1.4;
}

.cta-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  justify-content: center;
  margin-bottom: 2rem;
}

.btn {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  text-align: center;
}

.btn-primary {
  background: #4CAF50;
  color: white;
}

.btn-primary:hover {
  background: #45a049;
  transform: translateY(-2px);
}

.btn-secondary {
  background: transparent;
  color: white;
  border: 2px solid white;
}

.btn-secondary:hover {
  background: white;
  color: #667eea;
  transform: translateY(-2px);
}

.features-section {
  margin: 0;
  padding: 0;
}

.features-section h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.features-grid {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.feature-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin: 0;
}

.feature-card h3 {
  margin-bottom: 0.5rem;
  color: #4CAF50;
  font-size: 1rem;
}

.feature-card p {
  opacity: 0.9;
  line-height: 1.4;
  font-size: 0.8rem;
  margin: 0;
}

.communities-section {
  margin-top: 2rem;
}

.communities-section h2 {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.communities-list {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.community-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.community-info h3 {
  margin: 0 0 0.3rem 0;
  color: #4CAF50;
  font-size: 1rem;
}

.community-rules {
  color: #94a3b8;
  font-size: 0.7rem;
  margin: 0;
}

.btn-small {
  padding: 6px 12px;
  font-size: 0.8rem;
  min-width: 60px;
} 